@import url('https://fonts.googleapis.com/css2?family=Sacramento&display=swap');

.App {
  display: flex;
}

.screen-header {
  background: #01d8b0;
  border: solid 1px rgba(0, 0, 0, 0.12);
  box-shadow: rgba(0, 0, 0, .5) 0 -22px 25px 25px;
  color: white;
  z-index: 1;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  transition: top .5s;

  .inner {
    position: relative;
    top: 0;
    transition: top .5s;
  }

  h1 {
    margin: .3em;
    font-size: 1.3em;
    transition: .1s;
  }

  a {
    color: white;
    text-decoration: none;
  }

  .back-navigation {
    position: absolute;
    left: .5em;
    top: 50%;
    margin-top: -.5em;
    vertical-align: middle;
    line-height: 1em;
    font-size: 1.5em;
    font-weight: bold;
    width: 2em;
    box-sizing: border-box;
    text-align: left;
  }

  & + * > main{
    padding-top: 2.5em;
  }

  &.transparent {
    top: -2.6em;
    box-shadow: 0 0 35px 35px rgba(0,0,0,0.5);

    .inner {
      top: 2.5em;
    }

    & + * > main{
      padding-top: 0;
    }
  }

  &.big {
    h1 {
      font-size: 2.4em;

      &.app-title{
        font-family: 'Sacramento', cursive;
        margin: 0.2em 0 0.3em;
      }
    }

    & + * > main{
      padding-top: 4.8em;
    }
  }

  &.center {
    text-align: center;
  }
}

main {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  flex: 1;
  transition: .2s;
}

.button, .input {
  border: solid 1px rgba(0, 0, 0, .12);
  display: inline-block;
  line-height: 1.3em;
  vertical-align: middle;
  font-weight: bold;
  padding: 1em;
  margin: .5em;
  box-sizing: border-box;
  border-radius: 9999px;
  text-decoration: none;

  &.big {
    font-size: 1.3em;
  }

  .icon {
    font-size: 1.3em;
    line-height: 1em;
    margin: 0 .5em 0 -.5em;
    vertical-align: middle;
  }
}

.button {
  background: #01d8b0;
  box-shadow: rgba(0, 0, 0, .2) 0 0 10px -1px;
  color: white;
}

.input {
  background: white;
  box-shadow: rgba(0, 0, 0, .2) 0 0 10px -1px inset;
  color: #333;
  outline: none;

  &:focus {
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1) inset, 0 0 15px 0 #00f3ff;
  }
}