main.join {
  background: white;

  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;

  .inputs {
    transition: margin-top 1s;

    .individual-number {
      vertical-align: middle;
      line-height: 1em;
      font-size: 2em;
      display: inline-block;
      width: 1em;
      height: 1em;
      padding: .2em;
      text-align: center;
      border: solid 2px #01d8b0;
      color: #007a64;
      box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1) inset;
      border-radius: 5px;
      margin: .2em;
      font-weight: bold;
    }

    &.focus {
      margin-top: -13em;

      .individual-number.last {
        box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1) inset, 0 0 15px 0 #00f3ff;
      }
    }
  }
}