@import url(https://fonts.googleapis.com/css2?family=Sacramento&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{display:flex}.screen-header{background:#01d8b0;border:solid 1px rgba(0,0,0,0.12);box-shadow:rgba(0,0,0,0.5) 0 -22px 25px 25px;color:white;z-index:1;position:fixed;top:0;left:0;width:100%;transition:top .5s}.screen-header .inner{position:relative;top:0;transition:top .5s}.screen-header h1{margin:.3em;font-size:1.3em;transition:.1s}.screen-header a{color:white;text-decoration:none}.screen-header .back-navigation{position:absolute;left:.5em;top:50%;margin-top:-.5em;vertical-align:middle;line-height:1em;font-size:1.5em;font-weight:bold;width:2em;box-sizing:border-box;text-align:left}.screen-header+*>main{padding-top:2.5em}.screen-header.transparent{top:-2.6em;box-shadow:0 0 35px 35px rgba(0,0,0,0.5)}.screen-header.transparent .inner{top:2.5em}.screen-header.transparent+*>main{padding-top:0}.screen-header.big h1{font-size:2.4em}.screen-header.big h1.app-title{font-family:'Sacramento', cursive;margin:0.2em 0 0.3em}.screen-header.big+*>main{padding-top:4.8em}.screen-header.center{text-align:center}main{position:absolute;top:0;left:0;width:100%;min-height:100%;box-sizing:border-box;display:flex;flex-direction:column;flex:1 1;transition:.2s}.button,.input{border:solid 1px rgba(0,0,0,0.12);display:inline-block;line-height:1.3em;vertical-align:middle;font-weight:bold;padding:1em;margin:.5em;box-sizing:border-box;border-radius:9999px;text-decoration:none}.button.big,.input.big{font-size:1.3em}.button .icon,.input .icon{font-size:1.3em;line-height:1em;margin:0 .5em 0 -.5em;vertical-align:middle}.button{background:#01d8b0;box-shadow:rgba(0,0,0,0.2) 0 0 10px -1px;color:white}.input{background:white;box-shadow:rgba(0,0,0,0.2) 0 0 10px -1px inset;color:#333;outline:none}.input:focus{box-shadow:0 0 10px 0 rgba(0,0,0,0.1) inset,0 0 15px 0 #00f3ff}

main.frontpage{text-align:center;display:flex;justify-content:center;flex-direction:column}main.frontpage .banner{position:absolute;top:25%;text-align:center;width:100%;font-size:2.5em;margin-top:-0.2em;line-height:1em;text-shadow:3px 6px 12px rgba(0,0,0,0.3)}

main.room{background:white;position:relative}main.room .local-video-container{overflow:hidden;position:absolute;width:100%;height:100%;transition:.5s}main.room .local-video-container .local-video{position:absolute;top:0;left:0;width:100%;height:100%;background:black}main.room .remote-video-container{position:absolute;width:100%;height:100%;top:0;left:0}main.room .remote-video-container .remote-video{position:absolute;top:0;left:0;width:100%;height:100%;background:black}main.room .remote-video-container .remote-video video{position:absolute;display:block;width:100%;height:100%;object-fit:cover;transition:.3s}main.room .controls{position:absolute;bottom:0;width:100%;display:flex;flex-direction:column;box-shadow:0 0px 30px 35px rgba(0,0,0,0.5);background:rgba(0,0,0,0.5);transition:bottom .5s}main.room .controls.is-room-private{flex-direction:row}main.room .controls.is-room-private label{position:absolute;margin:0 0 0 2.5em;color:#fff;border-left:solid 1px #14d8b0;padding-left:0.5em}main.room .controls.is-room-private label.wrong{color:#ff9c9c}main.room .controls.is-room-private .control{margin-top:1.2em;margin-bottom:1em}main.room .controls.is-room-private .control.password{flex:1 1;min-width:0;margin-right:0;border-radius:9999px 0 0 9999px}main.room .controls.is-room-private .control.enter{min-width:4em;margin-left:0;border-radius:0 9999px 9999px 0}main.room .controls.is-room-private .control.enter .icon{display:none}main.room .controls .control{margin-top:1em;margin-bottom:1em}main.room.entered .controls{bottom:-10em}main.room.entered .local-video-container{overflow:hidden;position:absolute;width:30%;height:0;padding-bottom:30%;bottom:1em;left:1em;border-radius:5px;box-shadow:0 0 8px 4px rgba(0,0,0,0.6)}footer{z-index:1;position:relative;background:white}

.loading-container .spinner{position:absolute;top:0;left:0;width:100%;height:100%;background:rgba(0,0,0,0.3);display:flex;flex-direction:column;justify-content:center;z-index:99999}.loading-container .spinner:after{display:block;width:1em;height:1em;line-height:1em;text-align:center;color:white;font-weight:bold;content:'↻';font-size:8em;-webkit-animation:spin infinite linear 1s;animation:spin infinite linear 1s;margin:0 auto;padding:0.0em 0.0em 0.1em 0.0em}@-webkit-keyframes spin{100%{transform:rotate(360deg)}}@keyframes spin{100%{transform:rotate(360deg)}}

.advertisement{height:100px;box-sizing:border-box;border:solid 1px #ddd;text-align:center;display:flex;flex-direction:column;justify-content:center}

.local-video{position:relative;background:black}.local-video video{position:absolute;display:block;width:100%;height:100%;object-fit:cover;transition:.3s}.local-video.unloaded video{opacity:0}

.transition.popup{position:fixed;width:100%;height:100%;top:0;left:0}.transition.popup.top{top:-100%}.transition.popup.right{left:100%}.transition.popup.bottom{top:100%}.transition.popup.left{left:-100%}

main.join{background:white;text-align:center;display:flex;flex-direction:column;justify-content:center}main.join .inputs{transition:margin-top 1s}main.join .inputs .individual-number{vertical-align:middle;line-height:1em;font-size:2em;display:inline-block;width:1em;height:1em;padding:.2em;text-align:center;border:solid 2px #01d8b0;color:#007a64;box-shadow:0 0 10px 0 rgba(0,0,0,0.1) inset;border-radius:5px;margin:.2em;font-weight:bold}main.join .inputs.focus{margin-top:-13em}main.join .inputs.focus .individual-number.last{box-shadow:0 0 10px 0 rgba(0,0,0,0.1) inset,0 0 15px 0 #00f3ff}

main.create{background:white}

